import './App.css';
import { Layout } from 'antd';
import 'antd/dist/antd.css'
import Tab from './Components/Tab'
import React, {Component} from 'react'
import Orders from './Components/Orders'
import NewOrders from './Components/NewOrders'
import Products from './Components/Products'
import AddProduct from './Components/AddProduct'
import FeaturedProduct from './Components/FeaturedProduct'
import InvoicePrinter from './Components/InvoicePrinter'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { OrdersProvider } from './OrdersContext' 
import Deals from './Components/Deals';
import Category from './Components/Category';
const { Sider, Content } = Layout;
// var products =[]
var db


class App extends Component {
  state = {orders : [],}

  render() {
    return (
      <OrdersProvider order={this.state.orders}>
      <Router>
      <Layout>
        <Sider style={{width:'350px'}}>
          <Tab />
        </Sider>
        <Layout style={{height:'100vh'}}>
          <Content
            style={{
              margin: '26px 12vh',
              padding: 24,
              minHeight: 280,
            }}
          >
              <Switch>
                        <Route exact path='/' component={NewOrders}></Route>
                        <Route exact path='/processed' component={Orders}></Route>
                        <Route exact path='/products' component={Products}></Route>
                        <Route exact path='/invoice' component={InvoicePrinter}></Route>
                        <Route exact path='/addproduct' component={AddProduct}></Route>
                        <Route exact path='/featuredproduct' component={FeaturedProduct}></Route>
                        <Route exact path='/deals' component={Deals}></Route>
                        <Route exact path='/categories' component={Category}></Route>
              </Switch>
          </Content>
        </Layout>
      </Layout>
    </Router>
    </OrdersProvider>
    );
  }
}

export default App